@layer base {
	// Unordered/Ordered list
	ul,
	ol {
		padding: 0;
		margin-top: 0;
		@apply ml-4;
		@apply mb-6;

		ul,
		ol {
			margin-bottom: 0;
			list-style-type: inherit;
		}

		ul {
			list-style-type: circle;

			ul {
				list-style-type: square;
			}
		}
	}

	// Definition list
	dl {
		padding: 0;
		margin-top: 0;
	}

	dt {
		font-weight: bold;
		margin-bottom: 0;
	}

	dd {
		@apply ml-6;
		@apply mb-6;
	}
}
